<template>
  <header>
    <div class="header clearfix ">
      <div class="float-l h-left clearfix">
        <span class="float-l">
          <a :href="indexSippingoil">船加油首页</a>
        </span>
        <span class="float-l">联系电话 :&nbsp;010-56088802</span>
      </div>
      <div class="float-r h-right clearfix">
        <span>您好，{{ companyName }}
          <a :href="login" v-show="loginBtn" class="loginbtn">&nbsp;&nbsp;&nbsp;请登录</a>
        </span>
        <span v-show="out"><a :href="logout">退出</a></span>
        <span class="float-r">
          <span class="pointer" @click="OILEnglish">International</span>&nbsp;|&nbsp;
          <span @click="OILChinese" class="orgin pointer">中文站</span>
        </span>
        <span class="float-r">
          <div class="personlist">
            <transition name="fade">
              <el-dropdown>
                <span class="el-dropdown-link" style="font-size:12px">个人中心
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item>
                    <span @click="check(userMessage)">账户信息</span>
                  </el-dropdown-item>
                  <el-dropdown-item>
                    <span @click="check(userSecurity)">账号安全</span>
                  </el-dropdown-item>
                  <el-dropdown-item>
                    <span @click="check(resetPassword)">修改密码</span>
                  </el-dropdown-item>
                  <el-dropdown-item>
                    <span @click="check(subaccountUrl)" v-if="subaccount">子账户管理</span>
                  </el-dropdown-item>
                  <el-dropdown-item>
                    <span @click="check(enterpriseUrl)" v-if="enterprise">绑定企业</span>
                  </el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </transition>
          </div>
        </span>
        <span class="float-r">
          <div class="personlist">
            <transition name="fade">
              <el-dropdown>
                <span class="el-dropdown-link" style="font-size:12px">卖家中心
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item>
                    <router-link :to="{name: 'sl-manage'}">我的商品</router-link>
                  </el-dropdown-item>
                  <el-dropdown-item>
                    <router-link :to="{name: 'or-sellup'}">我的订单</router-link>
                  </el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </transition>
          </div>
        </span>
        <span class="float-r">
          <div class="personlist">
            <transition name="fade">
              <el-dropdown>
                <span class="el-dropdown-link" style="font-size:12px">买家中心
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item>
                    <router-link :to="{name: 'by-manage'}">我的采购</router-link>
                  </el-dropdown-item>
                  <el-dropdown-item>
                    <router-link :to="{name: 'or-buyup'}">我的订单</router-link>
                  </el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </transition>
          </div>
        </span>
      </div>
    </div>
  </header>
</template>

<script>
import url from "@/api/api_or";
import Utils from "@/util/login";
export default {
  name: "headBar",
  data() {
    return {
      out: true,
      username: "",
      companyName: "",
      loginBtn: false,
      subaccount: false,
      enterprise: false,
      subaccountUrl: url.subaccountUrl,
      enterpriseUrl: url.enterpriseUrl,
      indexSippingoil: url.indexSippingoil, //首页
      logout: url.logout, //退出登录
      login: url.login, //登录
      userMessage: url.userMessage, //账户信息
      userSecurity: url.userSecurity, //账户安全
      resetPassword: url.resetPassword, //修改密码
      sellGoods: url.sellGoods, //我的商品
      sellOrder: url.sellOrder, //卖家订单
      buyPurchase: url.buyPurchase, //我的采购
      buyOrder: url.buyOrder, //买家订单
      language1: url.chinese,
      language2: url.english
    };
  },
  created() {
    this.$http(url.selctLogin, {}, "get").then(res => {
      var data = res.data.datas;
      var message = res.data.message;
      if (message == "请登录") {
        this.loginBtn = true;
        this.out = false;
        this.companyName = "游客";
      } else if (message == "成功") {
        this.companyName = data.name;
        this.loginBtn = false;
        this.out = true;
        if (!data.roles.includes("enterprise")) {
          this.enterprise = true;
        }
        if (
          data.roles.includes("sales_trader") &&
          data.roles.includes("buy_trader")
        ) {
          document.cookie = "epMemberId=" + data.epMemberId;
        } else if (data.roles.includes("buy_trader")) {
          document.cookie = "epMemberId=" + data.epMemberId;
        } else if (data.roles.includes("sales_trader")) {
          document.cookie = "epMemberId=" + data.epMemberId;
        } else {
          return;
        }
        if (
          data.roles.includes("enter_admin") ||
          data.roles.includes("enter_master")
        ) {
          this.subaccount = true;
        }
        if (!data.roles.includes("enterprise")) {
          this.enterprise = true;
        }
      }
    });
  },
  methods: {
    OILEnglish() {
      document.cookie = "_l_=zh_CN;domain=.mycrudeoil.com;path=/";
      window.location.href = this.language2;
    },
    OILChinese() {
      document.cookie = "_l_=en_US;domain=.mycrudeoil.com;path=/";
      window.location.href = this.language1;
    },
    check(url) {
      if (!Utils.getCookie("_r")) {
        this.$message("请登录");
      } else if (Utils.getCookie("_r") == 0) {
        this.$message("请先绑定企业");
      } else {
        window.location.href = url;
      }
    }
  }
};
</script>

<style lang="scss">
.pointer{
  cursor: pointer;
}
.tabbar {
  line-height: 100px;
  img {
    margin: 26px 20px 0 0;
  }
  ul li {
    float: left;
    font-size: 16px;
    padding: 0 12px 0 76px;
  }
}
.tit {
  font-size: 18px;
  padding-right: 100px;
}
.active {
  color: #f36523;
}
.orgin {
  color: #f36523;
}
header {
  width: 100%;
  height: 34px;
  background: #eeeeee;
}
.show {
  position: absolute;
  z-index: 999;
  background: #ffffff;
}
.header {
  width: 1180px;
  margin: 0 auto;
  line-height: 34px;
  height: 34px;
  color: #333333;
  font-size: 12px;
  li {
    color: #666666;
  }
}
.h-left {
  span:first-of-type {
    font-size: 12px;
    color: #333333;
    font-weight: bold;
    margin-right: 46px;
    cursor: pointer;
  }
  span:nth-of-type(2) {
    font-size: 12px;
    color: #666666;
  }
}
.h-right {
  font-size: 12px;
  span {
    &:nth-of-type(1) {
      margin-right: 10px;
    }
    &:nth-of-type(2) {
      margin-right: 30px;
      cursor: pointer;
      &:hover {
        color: #ff9d32;
      }
    }
    &:nth-of-type(4) {
      margin-right: 20px;
      cursor: pointer;
    }
    &:nth-of-type(5) {
      cursor: pointer;
      &:hover {
        color: #ff9d32;
      }
    }
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.personlist {
  position: relative;
  ul {
    position: absolute;
    left: -30px;
    z-index: 9999;
    box-shadow: 0 0 3px #ececec;
    li {
      width: 100px;
      padding: 0 3px;
      border: 1px solid #ececec;
      border-top: 0;
      text-align: center;
      a {
        display: block;
        width: 100%;
        height: 100%;
      }
      &:hover {
        background: #f36523;
        color: #fff;
      }
    }
  }
}
.loginbtn {
  color: #f36523;
}
.el-dropdown-link {
  cursor: pointer;
  &:hover {
    color: #f36523;
  }
}
</style>


